'use client';

import { useEffect, useMemo, useState } from 'react';
import { useRouter } from 'next/navigation';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import { Label } from '@/components/ui/label';

interface BeneficiaryRequestResponseInput {
  responseId: string; // parent request id (FK)
  responseText: string;
}

interface InitialData {
  id: string; // <-- the response row's id (PK)
  responseId: string; // parent request id (FK)
  responseText: string;
}

export default function CreateBeneficiaryRequestResponseForm({
  responseId, // parent request id for CREATE
  editId, // response row id for EDIT (optional if initialData is provided)
  initialData,
  onSuccess,
  onCancel,
}: {
  responseId: string;
  editId?: string;
  initialData?: InitialData;
  onSuccess?: () => void;
  onCancel?: () => void;
}) {
  const router = useRouter();

  // Use initialData.id as a fallback when editId is not explicitly passed
  const effectiveEditId = useMemo(() => editId ?? initialData?.id, [editId, initialData?.id]);
  const isEditing = Boolean(effectiveEditId && `${effectiveEditId}`.trim().length > 0);

  const [form, setForm] = useState<BeneficiaryRequestResponseInput>({
    responseId: responseId ?? '',
    responseText: '',
  });
  const [loading, setLoading] = useState(false);
  const [loadingInitial, setLoadingInitial] = useState(isEditing && !initialData);

  useEffect(() => {
    if (initialData) {
      // Populate from initialData; keep parent link stable
      setForm({
        responseId: initialData.responseId ?? responseId ?? '',
        responseText: initialData.responseText ?? '',
      });
      setLoadingInitial(false);
      return;
    }

    if (isEditing && effectiveEditId) {
      setLoadingInitial(true);
      fetch(`/api/beneficiaryRequestResponse/${effectiveEditId}`)
        .then((res) => {
          if (!res.ok) throw new Error('Failed to fetch response');
          return res.json();
        })
        .then((data) => {
          setForm({
            responseId: data.responseId ?? responseId ?? '',
            responseText: data.responseText ?? '',
          });
        })
        .catch(() => {
          alert('Error loading existing response data.');
        })
        .finally(() => setLoadingInitial(false));
    } else {
      setForm({
        responseId: responseId ?? '',
        responseText: '',
      });
    }
  }, [initialData, isEditing, effectiveEditId, responseId]);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setForm((prev) => ({ ...prev, [name]: value ?? '' }));
  };

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setLoading(true);

    try {
      // On EDIT, donâ€™t send responseId (parent FK) to avoid accidental relinks
      const payload = isEditing
        ? { responseText: form.responseText ?? '' }
        : { responseId: form.responseId ?? '', responseText: form.responseText ?? '' };

      const url = isEditing
        ? `/api/beneficiaryRequestResponse/${effectiveEditId}`
        : '/api/beneficiaryRequestResponse';

      const method = isEditing ? 'PATCH' : 'POST';

      const res = await fetch(url, {
        method,
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload),
      });

      if (!res.ok) {
        const error = await res.json().catch(() => ({}));
        throw new Error(error.error || 'Failed to save beneficiary request response');
      }

      onSuccess ? onSuccess() : router.refresh();
    } catch (err) {
      alert('There was an error saving the beneficiary request response.');
    } finally {
      setLoading(false);
    }
  };

  if (loadingInitial) {
    return (
      <div className="flex items-center justify-center min-h-40">
        <span className="text-gray-600">Loading...</span>
      </div>
    );
  }

  return (
    <form
      onSubmit={handleSubmit}
      className="max-w-xl mx-auto space-y-6 p-6 bg-background rounded-xl shadow"
      autoComplete="off"
    >
      <h2 className="font-semibold text-xl mb-4">
        {isEditing ? 'Edit Beneficiary Request Response' : 'Create Beneficiary Request Response'}
      </h2>

      {/* Hidden parent request id: used only for CREATE */}
      <Input type="hidden" name="responseId" value={form.responseId ?? ''} />

      <div className="space-y-2">
        <Label htmlFor="responseText">Response Text (JSON or text)</Label>
        <textarea
          id="responseText"
          name="responseText"
          value={form.responseText ?? ''}
          onChange={handleChange}
          required
          disabled={loading}
          className="block w-full rounded border px-2 py-1 min-h-[100px]"
        />
      </div>

      <div className="flex gap-3">
        <Button
          type="submit"
          disabled={loading}
          className="flex-1 bg-[#9f004d] hover:bg-[#7c003c] text-white"
        >
          {loading
            ? isEditing
              ? 'Saving...'
              : 'Creating...'
            : isEditing
            ? 'Save Changes'
            : 'Create Response'}
        </Button>
        {onCancel && (
          <Button
            type="button"
            variant="outline"
            className="flex-1 border-[#9f004d] text-[#9f004d] hover:bg-[#f5e3ec]"
            onClick={onCancel}
            disabled={loading}
          >
            Cancel
          </Button>
        )}
      </div>
    </form>
  );
}
